  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 700;
  transition: 0.4s;
}
.btn i {
  padding-left: 11px;
  column-rule: inherit;
  font-size: 14px;
  transition: 0.4s;
}
.btn:hover i {
  transform: translateX(15%);
}
.btn-primary {
