  }
}
.site-header__bottom .header-btn i {
  margin-right: 8px;
}
@media (max-width: 991px) {
  .site-header__bottom {
    /* 1) Stop Bootstrap's height/display animation (causes jump) */
    /* 2) Backdrop (hidden by default) */
    /* 3) Drawer panel */
    /* 4) Visible/open state: Bootstrap adds .show */
    /* 5) Show backdrop only when menu is open */
    /* 6) Optional: lock scroll (prevents layout shift) */
  }
  .site-header__bottom .navbar-brand {
    max-width: 180px;
  }
  .site-header__bottom .navbar-toggler {
    order: 3;
  }
  .site-header__bottom .navbar .collapse {
    display: block !important;
    /* keep mounted; we'll animate with transform */
    height: 100% !important;
    overflow: visible !important;
    visibility: visible !important;
  }
  .site-header__bottom .navbar .collapsing {
    height: 100% !important;
    transition: none !important;
    /* kill height tween */
  }
  .site-header__bottom .menu-backdrop {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.4);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.25s ease, visibility 0.25s ease;
    z-index: 1049;
    /* under the panel (1050) */
  }
  .site-header__bottom .main-menu {
    position: fixed;
    inset: 0 auto 0 0;
    /* top:0; right:auto; bottom:0; left:0 */
    background-color: #fff;
    width: 100%;
    max-width: 320px;
    height: 100%;
    padding: 100px 30px 30px;
    transform: translateX(-100%);
    /* hidden off-canvas */
    transition: transform 0.3s ease;
    z-index: 1050;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    will-change: transform;
    backface-visibility: hidden;
    pointer-events: none;
    /* prevent stray taps when hidden */
    z-index: 99999;
  }
  .site-header__bottom .main-menu .navbar-nav {
    width: 100%;
  }
  .site-header__bottom .main-menu .btn-close {
    position: absolute;
    right: 16px;
    top: 16px;
  }
  .site-header__bottom .main-menu.show {
    transform: translateX(0);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
    pointer-events: auto;
  }
  .site-header__bottom .main-menu.show ~ .menu-backdrop {
    opacity: 1;
    visibility: visible;
  }
  .site-header__bottom body.menu-open {
    overflow: hidden;
    touch-action: none;
    -webkit-overflow-scrolling: auto;
  }
}

