  width: 100%;
}
.kd-gallery-thumb::before {
  content: "";
  height: 100%;
  width: 100%;
  background: linear-gradient(180deg, #343742 0%, #090B18 100%);
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 16px;
  opacity: 0;
  visibility: hidden;
  transition: 0.4s;
  z-index: 1;
}
