  transform: scale(1.06);
}
.kd-gallery-thumb-wrapper:hover .kd-gallery-icon {
  opacity: 1;
  visibility: visible;
  transition-delay: 0.2s;
}
.kd-gallery-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
